.char__info{padding:25px;background-color:#fff;box-shadow:0px 0px 20px 0px rgba(0,0,0,.25);position:sticky;top:5px;z-index:5}.char__info-name{font-weight:bold;font-size:22px;line-height:29px;text-transform:uppercase;margin-bottom:35px}.char__basics{display:grid;grid-template-columns:150px auto;column-gap:25px}.char__basics img{width:150px;height:150px;object-fit:cover}.char__btns a:nth-child(2){margin-top:10px}.char__descr{margin-top:15px;font-size:14px;line-height:18px}.char__comics{margin-top:10px;font-size:18px;font-weight:bold;line-height:24px}.char__comics-list{margin-top:10px}.char__comics-item{width:100%;line-height:25px;padding:0px 10px;box-shadow:0px 4px 4px 0px rgba(0,0,0,.25);margin-top:10px}.char__comics-item:hover{box-shadow:2px 6px 6px 2px rgba(0,0,0,.5)}.fade-selectedСhar{position:sticky;top:20px}.fade-selectedСhar-enter{opacity:0;transform:scale(0.8, 0.8)}.fade-selectedСhar-enter-active{opacity:1;transform:scale(1, 1)}.fade-selectedСhar-exit{opacity:1;transform:scale(1, 1)}.fade-selectedСhar-exit-active{opacity:0;transform:scale(0.8, 0.8)}.fade-selectedСhar-enter-active,.fade-selectedСhar-exit-active{transition:all 300ms ease-in-out}